// client/src/MemberManagement.js
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

function MemberManagement({ operator }) {
  const navigate = useNavigate();
  const [members, setMembers] = useState([]);
  const [memberForm, setMemberForm] = useState({
    id: null,
    name: '',
    generation: '',
    "party": '',
    gender: '',
    department: '',
    "post": ""
  });
  const [isEditing, setIsEditing] = useState(false);
  const [message, setMessage] = useState('');
  const [logs, setLogs] = useState([]);

  // 
  const fetchMembers = () => {
    fetch('/api/members')
      .then(response => response.json())
      .then(data => setMembers(data))
      .catch(error => console.error('Error fetching members:', error));
  };

  // 
  const fetchLogs = () => {
    fetch('/api/memberLogs')
      .then(response => response.json())
      .then(data => setLogs(data))
      .catch(error => console.error('Error fetching logs:', error));
  };

  useEffect(() => {
    fetchMembers();
    fetchLogs();
  }, []);

  // 
  const resetForm = () => {
    setMemberForm({ id: null, name: '', generation: '', "party": '', "post": '', gender: '', department: '' });
    setIsEditing(false);
  };

  // 
  const handleSubmit = (e) => {
    e.preventDefault();
    if (isEditing) {
      // PUT operator 
      fetch(`/api/members/${memberForm.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: memberForm.name,
          generation: Number(memberForm.generation),
          party: memberForm.party,
          post: memberForm.post,
          gender: memberForm.gender,
          department: memberForm.department,
          operator // 
        })
      })
        .then(response => response.json())
        .then(updatedMember => {
          setMembers(members.map(m => (m.id === updatedMember.id ? updatedMember : m)));
          resetForm();
          fetchLogs();
        })
        .catch(error => {
          console.error('Error updating member:', error);
          setMessage('');
        });
    } else {
      // POST operator 
      fetch('/api/members', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: memberForm.name,
          generation: Number(memberForm.generation),
          party: memberForm.party,
          post: memberForm.post,
          gender: memberForm.gender,
          department: memberForm.department,
          operator // 
        })
      })
        .then(response => response.json())
        .then(newMember => {
          setMembers([...members, newMember]);
          resetForm();
          fetchLogs();
        })
        .catch(error => {
          console.error('Error creating member:', error);
          setMessage('');
        });
    }
  };

  // 
  const handleEdit = (member) => {
    setMemberForm(member);
    setIsEditing(true);
  };

  // DELETE operator 
  const handleDelete = (id) => {
    fetch(`/api/members/${id}`, {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ operator })
    })
      .then(response => response.json())
      .then(deletedMember => {
        setMembers(members.filter(m => m.id !== deletedMember.id));
        fetchLogs();
      })
      .catch(error => {
        console.error('Error deleting member:', error);
        setMessage('');
      });
  };

  // 
  const sortedMembers = [...members].sort((a, b) => b.generation - a.generation);

  return (
    <div style={{ padding: '20px' }}>
      <h1></h1>
      <button onClick={() => navigate('/tool/dashboard')}></button>
      
      {/*  */}
      <form onSubmit={handleSubmit} style={{ marginTop: '20px' }}>
        <div>
          <label>: </label>
          <input
            type="text"
            value={memberForm.name}
            onChange={(e) => setMemberForm({ ...memberForm, name: e.target.value })}
            required
          />
        </div>       
        <div>
          <label>: </label>
          <input
            type="text"
            value={memberForm.gender}
            onChange={(e) => setMemberForm({ ...memberForm, gender: e.target.value })}
            required
          />
        </div>
        <div>
          <label>: </label>
          <input
            type="text"
            value={memberForm.department}
            onChange={(e) => setMemberForm({ ...memberForm, department: e.target.value })}
            required
          />
        </div>
        <div>
          <label>: </label>
          <input
            type="number"
            value={memberForm.generation}
            onChange={(e) => setMemberForm({ ...memberForm, generation: e.target.value })}
            required
          />
        </div>
        <div>
          <label>: </label>
          <input
            type="text"
            value={memberForm.party}
            onChange={(e) => setMemberForm({ ...memberForm, party: e.target.value })}
            required
          />
        </div> 
        <div>
          <label>: </label>
          <input
            type="text"
            value={memberForm.post}
            onChange={(e) => setMemberForm({ ...memberForm, post: e.target.value })}
            required
          />
        </div> 
        <button type="submit">{isEditing ? '' : ''}</button>
        {isEditing && <button type="button" onClick={resetForm}></button>}
      </form>
      {message && <p>{message}</p>}
      
      {/*  */}
      <h2 style={{ marginTop: '20px' }}></h2>
      <ul>
        {sortedMembers.map(member => (
          <li key={member.id}>
            {member.name} - {member.gender} - {member.department} - {member.generation} - {member.party} - {member.post}
            <button onClick={() => handleEdit(member)} style={{ marginLeft: '10px' }}>
              
            </button>
            <button onClick={() => handleDelete(member.id)} style={{ marginLeft: '5px' }}>
              
            </button>
          </li>
        ))}
      </ul>
      
      {/*  */}
      <h2 style={{ marginTop: '20px' }}></h2>
      <ul style={{ height: '80px', overflow: 'auto' }}>
        {logs.map((log, index) => (
          <li key={index}>
            {log.timestamp} - {log.operator}  {log.memberName}  {log.operation} 
          </li>
        ))}
      </ul>
    </div>
  );
}

export default MemberManagement;
