// client/src/AccountManagement.js
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

function AccountManagement({ operator }) {
  const navigate = useNavigate();
  const [accounts, setAccounts] = useState([]);
  const [logs, setLogs] = useState([]);

  // 
  const [createAccountName, setCreateAccountName] = useState('');
  const [createPassword, setCreatePassword] = useState('');
  const [createMessage, setCreateMessage] = useState('');

  // 
  const [updateAccountName, setUpdateAccountName] = useState('');
  const [updateCurrentPassword, setUpdateCurrentPassword] = useState('');
  const [updateNewPassword, setUpdateNewPassword] = useState('');
  const [updateMessage, setUpdateMessage] = useState('');

  // 
  const [deleteAccountName, setDeleteAccountName] = useState('');
  const [deletePassword, setDeletePassword] = useState('');
  const [deleteMessage, setDeleteMessage] = useState('');

  // 
  const fetchAccounts = () => {
    fetch('/api/accounts')
      .then(response => response.json())
      .then(data => setAccounts(data))
      .catch(error => console.error('Error fetching accounts:', error));
  };

  // 
  const fetchLogs = () => {
    fetch('/api/account_logs')
      .then(response => response.json())
      .then(data => setLogs(data))
      .catch(error => console.error('Error fetching account logs:', error));
  };

  useEffect(() => {
    fetchAccounts();
    fetchLogs();
  }, []);

  // 
  const handleCreate = (e) => {
    e.preventDefault();
    fetch('/api/accounts', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        accountId: createAccountName,
        password: createPassword,
        operator
      })
    })
      .then(response => response.json())
      .then(data => {
        if (data.error) {
          setCreateMessage(`: ${data.error}`);
        } else {
          setCreateMessage('');
          setCreateAccountName('');
          setCreatePassword('');
          fetchAccounts();
          fetchLogs();
        }
      })
      .catch(error => {
        console.error('Error creating account:', error);
        setCreateMessage('');
      });
  };

  // 
  const handleUpdate = (e) => {
    e.preventDefault();
    fetch('/api/accounts', {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        accountId: updateAccountName,
        currentPassword: updateCurrentPassword,
        newPassword: updateNewPassword,
        operator
      })
    })
      .then(response => response.json())
      .then(data => {
        if (data.error) {
          setUpdateMessage(`: ${data.error}`);
        } else {
          setUpdateMessage('');
          setUpdateAccountName('');
          setUpdateCurrentPassword('');
          setUpdateNewPassword('');
          fetchAccounts();
          fetchLogs();
        }
      })
      .catch(error => {
        console.error('Error updating account:', error);
        setUpdateMessage('');
      });
  };

  // 
  const handleDelete = (e) => {
    e.preventDefault();
    fetch('/api/accounts', {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        accountId: deleteAccountName,
        currentPassword: deletePassword,
        operator
      })
    })
      .then(response => response.json())
      .then(data => {
        if (data.error) {
          setDeleteMessage(`: ${data.error}`);
        } else {
          setDeleteMessage('');
          setDeleteAccountName('');
          setDeletePassword('');
          fetchAccounts();
          fetchLogs();
        }
      })
      .catch(error => {
        console.error('Error deleting account:', error);
        setDeleteMessage('');
      });
  };

  return (
    <div style={{ padding: '20px' }}>
      <h1></h1>
      <button onClick={() => navigate('/tool/dashboard')}></button>
      
      {/*  */}
      <section style={{ marginTop: '20px' }}>
        <h2></h2>
        <ul>
          {accounts.map(acc => (
            <li key={acc.id}>
              ID: {acc.id} / : {acc.accountId}
            </li>
          ))}
        </ul>
      </section>
      
      {/*  */}
      <section style={{ marginTop: '20px', border: '1px solid #ccc', padding: '10px' }}>
        <h2></h2>
        <form onSubmit={handleCreate}>
          <div>
            <label>: </label>
            <input
              type="text"
              value={createAccountName}
              onChange={(e) => setCreateAccountName(e.target.value)}
              required
            />
          </div>
          <div>
            <label>: </label>
            <input
              type="password"
              value={createPassword}
              onChange={(e) => setCreatePassword(e.target.value)}
              required
            />
          </div>
          <button type="submit"></button>
        </form>
        {createMessage && <p>{createMessage}</p>}
      </section>

      {/*  */}
      <section style={{ marginTop: '20px', border: '1px solid #ccc', padding: '10px' }}>
        <h2></h2>
        <form onSubmit={handleUpdate}>
          <div>
            <label>: </label>
            <input
              type="text"
              value={updateAccountName}
              onChange={(e) => setUpdateAccountName(e.target.value)}
              required
            />
          </div>
          <div>
            <label>: </label>
            <input
              type="password"
              value={updateCurrentPassword}
              onChange={(e) => setUpdateCurrentPassword(e.target.value)}
              required
            />
          </div>
          <div>
            <label>: </label>
            <input
              type="password"
              value={updateNewPassword}
              onChange={(e) => setUpdateNewPassword(e.target.value)}
              required
            />
          </div>
          <button type="submit"></button>
        </form>
        {updateMessage && <p>{updateMessage}</p>}
      </section>

      {/*  */}
      <section style={{ marginTop: '20px', border: '1px solid #ccc', padding: '10px' }}>
        <h2></h2>
        <form onSubmit={handleDelete}>
          <div>
            <label>: </label>
            <input
              type="text"
              value={deleteAccountName}
              onChange={(e) => setDeleteAccountName(e.target.value)}
              required
            />
          </div>
          <div>
            <label>: </label>
            <input
              type="password"
              value={deletePassword}
              onChange={(e) => setDeletePassword(e.target.value)}
              required
            />
          </div>
          <button type="submit"></button>
        </form>
        {deleteMessage && <p>{deleteMessage}</p>}
      </section>

      {/*  */}
      <section style={{ marginTop: '20px' }}>
        <h2></h2>
        <ul style={{ height: '80px', overflow: 'auto' }}>
          {logs.map((log, index) => (
            <li key={index}>
              {log.timestamp} - {log.operator}  {log.accountName}  {log.operation} 
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
}

export default AccountManagement;
