import React, { useEffect, useRef } from 'react';
import ScrollReveal from 'scrollreveal';

function HomePage() {
    const homeImageRef = useRef(null);
    const articleRef = useRef(null);

    // IntersectionObserver 
    useEffect(() => {
        const observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    homeImageRef.current.classList.remove("backgroundNone");
                    homeImageRef.current.classList.add("backgroundDark");
                } else {
                    homeImageRef.current.classList.remove("backgroundDark");
                    homeImageRef.current.classList.add("backgroundNone");
                }
            });
        }, { threshold: 0.01 });

        if (articleRef.current) {
            observer.observe(articleRef.current);
        }
        return () => observer.disconnect();
    }, []);

    // ScrollReveal  useEffect
    useEffect(() => {
        ScrollReveal({ reset: true, distance: "60px", duration: 1000 });
        ScrollReveal().reveal('.post-info h3', { delay: 100 });
        ScrollReveal().reveal('.post-info p', { delay: 200 });
        ScrollReveal().reveal('article img', { delay: 200, origin: "left" });
        ScrollReveal().reveal('article .info-set', { delay: 200, origin: "bottom" });
        ScrollReveal().reveal('.aside-content .sub-menu li', { delay: 10, origin: "bottom", interval: 50, duration: 500 });
    }, []);

    return (
        <div id="home" className="big-bg">
            <div className="home-content wrapper">
                <img
                    ref={homeImageRef}
                    src="./kyudobu_img/HP_photo.jpg"
                    alt="HP"
                    className="backgroundNone"
                />
                <img src="./kyudobu_img/seisyaseityuu.svg" alt="Secondary" />
            </div>
            <section className="information">
                <p style={{ color: 'red', fontSize: '1.5rem', padding: '0 10%', margin: 0 }}>
                    
                </p>
                <ul style={{ padding: '0 10%' }}>
                    <li>Home Page (2/28/2024)</li>
                    <li>iOSCSS  background-attachment: fixed; (3/5/2024)</li>
                    <li>Home Page  JavaScript (3/9/2024)</li>
                    <li>(3/17/2024)</li>
                </ul>
                <article ref={articleRef}>
                    <header className="post-info">
                        <h3 className="sub-title"></h3>
                        <p>
                            <br />
                            
                        </p>
                    </header>
                    <aside>
                        <div className="aside-content">
                            <div className="aside-img"></div>
                            <ul className="sub-menu">
                                <li><a href="#"></a></li>
                                <li style={{ marginLeft: "10px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "20px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "30px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "40px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "50px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "60px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "70px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "80px" }}><a href="#"></a></li>
                                <li style={{ marginLeft: "90px" }}><a href="#OBOG">OBOG</a></li>
                            </ul>
                        </div>


                    </aside>
                    <div className="articlePadding">
                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                </p>
                            </div>
                        </div>
                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                     
                                </p>
                            </div>
                        </div>
                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    OB
                                </p>
                            </div>
                        </div>
                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                </p>
                            </div>
                        </div><div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    25
                                    <a href="Kyudo_schedule.html" style={{ color: '#2a1500' }}></a>
                                    
                                </p>
                            </div>
                        </div>

                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                    <a href="https://twitter.com/tcu_kyudo" style={{ color: '#2a1500' }}>X(Twitter)</a>
                                    
                                    <a href="https://www.instagram.com/tcu_kyudo/" style={{ color: '#2a1500' }}>Instagram</a>
                                    
                                </p>
                            </div>
                        </div>

                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                </p>
                            </div>
                        </div>

                        <div id="" className="info-set">
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                    
                                    
                                </p>
                            </div>
                        </div>

                        <div id="" className="info-set" style={{ marginBottom: '580px' }}>
                            <img></img>
                            <div className="info-text">
                                <h4></h4>
                                <p>
                                    
                                    
                                    
                                </p>
                            </div>
                        </div>

                        <div id="OBOG" className="info-set" style={{ marginBottom: '700px' }}>
                            <img></img>
                            <div className="info-text">
                                <h4>OBOG</h4>
                                <p>
                                    1155
                                    
                                    
                                    <span className="thank_line">
                                        OBOG
                                        
                                    </span>
                                    
                                    
                                </p>
                            </div>
                        </div>

                    </div>
                </article>
            </section>
        </div>
    );
}

export default HomePage;
